@import "./variables";
@import "./mixins";
@import "./color-pallete";

* {
  box-sizing: border-box;
}

img {
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

h1,
h2,
h3,
h4 {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
  font-family: "Varela Round", sans-serif;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #303d44;
  user-select: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: 100vh;
  max-height: 100%;
  @include theme-aware("background-color", "color-background-darker");
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.shadow-circle {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: -520px;
  box-shadow: 0 -5px 40px 0 rgba(30, 30, 30, 0.2);
  @include theme-aware("background-color", "color-background");
  overflow: hidden;
  width: 1100px;
  height: 900px;
  border-radius: 50%;
  transition: all 0.8s ease-in-out;
  z-index: 2;
}

.shadow-circle.opened {
  top: -250px;
}

.checkmark {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: green;
  stroke-miterlimit: 10;
  stroke-dashoffset: 0;
  margin: auto;
}

header {
  font-weight: 600;
  font-size: 2em;
  @include theme-aware("color", "color-text");
  margin: 1em 0;
  padding: 0 1em;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 5;

  i {
    font-size: 0.8em;

    &:hover {
      cursor: pointer;
    }
  }
}

.timer {
  width: 300px;
  min-width: 300px;
  height: 300px;
  min-height: 300px;
  border-radius: 50%;
  background-image: radial-gradient($colorPrimary, $colorPrimaryShaded);
  @include theme-aware("color", "color-text");
  font-weight: bold;
  box-shadow: 0 0 10px 0 #f8c300;
  display: flex;
  flex-direction: column;
}

/* Timers */

.timers-headers {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding: 0 1em;
}

.timers-headers > div {
  text-transform: uppercase;
  @include theme-aware("color", "color-header");
  font-size: 0.8em;
  font-weight: 500;
  width: 18%;
}

.timers {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 5px;
  flex-wrap: wrap;
  margin: 10px 10px;
}

.timer-div {
  width: 18%;
}

@import "./timer-selector";
@import "./theme-switch";

.timers-panel {
  display: flex;
  position: absolute;
  bottom: 30px;
  flex-direction: column;
  width: 390px;
  transition: all 0.8s ease-in-out;
  padding-bottom: 30px;
  z-index: 1;
}

.timer-body {
  @include theme-aware("background", "color-background");
  @include theme-aware("color", "color-primary");
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.timer-body.inactive {
  background: radial-gradient($colorPrimary, $colorPrimaryShaded);
  @include theme-aware("color", "color-background-darker");
}

.timer-clock {
  font-size: 3em;
}

.timer-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 330px;
  @include theme-aware("background-color", "color-background");
  z-index: 3;
}

.timer-wrapper {
  z-index: 4;

  svg {
    path:first-child {
      transition: $transition;
    }
  }
}

.i-chevron {
  position: absolute;
  bottom: 0;
  z-index: 1;
  font-size: 3em;
  margin: 0 auto;
  @include theme-aware("color", "color-text");
  transition: all 0.8s ease-in-out;
}

.i-chevron.opened {
  transform: scaleY(-1);
}

.timers-table {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 5px;
  flex-wrap: wrap;
}

.timer-btn {
  @include theme-aware("color", "color-primary");
  border: 1px solid $colorPrimary;
  @include theme-aware("background-color", "color-background");
  width: 18%;
  border-radius: 5px;
  padding: 0.5em 0;
}

.timer-btn.current {
  @include theme-aware("color", "color-text-contrast");
  @include theme-aware("background-color", "color-primary");
}

// Weather stats
.weather-stats-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 120px;
  gap: 1em;
  row-gap: 2em;
  padding: 0 1em;
  z-index: 0;

  .weather-stat {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1.5em;
    width: 47%;
    @include theme-aware("color", "color-text");

    i {
      font-size: 2em;
    }

    .weather-stat-data {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 0.3em;

      .stat-value {
        font-size: 1.5em;
      }

      .stat-name {
        font-size: 0.7em;
        text-transform: uppercase;
        @include theme-aware("color", "color-header");
        letter-spacing: 0.5px;
      }
    }
  }
}
